main {
  min-height: 60vh;
}

h3 {
  padding: 1rem 0;
}

.rating span {
  margin: 0.1rem;
}

.text-small {
  font-size: 0.9rem !important;
}

.nav-link {
  color: rgb(255, 253, 253) !important;
}

.nav-link:hover {
  color: rgb(167, 164, 164) !important;
}

.header {
  background-color: #00d2ff;
  background-image: linear-gradient(to right, #00d2ff 0%, #3a7bd5 100%);
}

.section-1 {
  background: #f5f5f5;
}

.section-2 {
  background: #f1f1f1;
}

.section-3 {
  background: #eaeaea;
}

.section-4 {
  background: #e5e5e5;
}

.stepCompleted {
  color: rgb(0, 0, 0) !important;
  text-decoration: none !important;
}

.stepNotCompleted {
  color: rgb(173, 173, 173) !important;
  text-decoration: none !important;
}

/* carousel */

.carousel-item-next,
.carousel-item-prev,
.carousel-item.active {
  display: flex;
}

.carousel-caption {
  position: absolute;
  top: 0;
}

.carousel-caption h2 {
  color: #fff;
}

.carousel img {
  height: 300px;
  padding: 30px;
  margin: 40px;
  border-radius: 50%;
}

.carousel a {
  margin: 0 auto;
}

.modal-90w {
  width: 50vw;
  max-width: 50vw;
}

.searchBar {
  color: #333;
  display: table;
  background-color: #febb02;
  padding: 4px;
  border-radius: 4px;
  margin: auto;
  position: relative;
  width: 70%;
}

.searchbutton:focus {
  outline: none !important;
  box-shadow: none !important;
}

.searchbutton {
  background-color: rgb(17, 101, 181);
  color: rgb(255, 255, 255);
  border: none;
  padding: 0px 30px;
}

.searchbutton:hover {
  background-color: rgb(16, 78, 136);
  color: white;
}

.equipmentcard {
  padding: 8px !important;
  background-color: #96969628 !important;
  box-shadow: none !important;
}

.noMousePointer {
  pointer-events: none;
  cursor: default;
}
